import React from "react"

import Layout from "../components/layout"
import { graphql } from "gatsby"
import Img from "gatsby-image"
import styles from "./index.module.css"
import SEO from '../components/SEO/SEO'
import FadeIn from "../utils/fade-in";

export default ({ data }) => (
  <Layout>
    <div className={styles.main_h}>
      <SEO banner={data.file.childImageSharp.fluid.src} />
      <div className={styles.content}>
        <FadeIn
          x={{
            start: 70,
            end: 0,
            stiffness: 50,
            damping: 20
          }}
        >
          <h1>About</h1>
        </FadeIn>
        <p>Yao majored in physics during college and studied in electrical engineering for a master degree at National Taiwan University. In this period, he cooperated with TSMC to research semiconductor related fields, and published his works in AIP Advances as the first author. He also has some basics in the fields related to artificial intelligence and front-end web development. 
        </p>
        <p>In addition, his father is an art lover with artistic background who taught him to draw as he was young. The sketch 《Most beautiful equation in math》 has been invited and published in 《Science Monthly》 in Taiwan. In the future, he hopes to use what he has learned to merge the spirit of science and technology into art, our society, and daily life.
        </p>
        <p>Email: tomtogo1260@gmail.com</p>
      </div>
      <div className={styles.image}>
        <Img fluid={data.file.childImageSharp.fluid} />
      </div>
    </div>
  </Layout>
)

export const query = graphql`
  query {
    site {
      siteMetadata {
        title
        description
      }
    }
    file(relativePath: {eq: "about.jpg"}) {
      childImageSharp {
        fluid(maxWidth: 500) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`